
import { defineComponent } from "vue";
import App from "../main";
export default defineComponent({
    name: "Directive",
    setup() {
        App.$webtrekk.extension("");
    },
    data() {
        return {
            activeDirective: "none",
            showTeaser: false,
            directiveList: [
                "pageDir",
                "noModifiers",
                "productViewDir",
                "productBasketDir",
                "productConfirmationDir",
                "productListDir",
                "productsDir",
                "productsDirNoStatus",
                "actionDir",
                "teaserDir",
                "teaserWithSelectorDir",
                "productListExtensionDir",
                "contentEngagementDir"
            ]
        };
    },
    methods: {
        clickHandler: function(dir) {
            this.activeDirective = dir;
            if (dir === "teaserWithSelectorDir") {
                window.setTimeout(() => {
                    this.showTeaser = true;
                }, 1000);
            }
        }
    }
});
